
module.exports = {
	
	// api  url
	apiBaseUrl        : 'http://192.168.3.4:8081/',
	// apiBaseUrl        : 'https://xjsf.chalinong.com/',
	
	//  [ false  false ]
	debug             : false,
	
	//  Token 
	localTokenKeyName : 'reqToken',
	
	//  Token 
	userTokenKeyName  : 'uToken',
	
	// token ,   
	expiredTime       : 3600,
	
	// post  header[content-type] 
	postHeaderDefault : 'application/json',
	
	//  token 
	//  api  token 
	// 	
	getToken : (resolve, reject) => {
		
		//  
		//  : 
		var appKey = 'AppKey2021&&';
		
		// 
		uni.request({
			// token  api 
			url      : "https://www.******.com/api/srequest/makeToken",
			// 
			data     : { 'appKey' : appKey },
			//  POST
			method   : "POST",
			// headers
			header   : {'content-type' : 'application/x-www-form-urlencoded'},
			// 
			dataType : "json",
			// 
			success  : (res) => {
				var data = res.data;
				if(data.status == 'ok'){
					resolve(data.data);
				}else{
					reject();
				}
			},
			// 
			fail     : (e) => { reject();}
		});
	}
	
}
